.diagramm {
  background: url("../img/frukty.png") -24px bottom no-repeat;
  position: relative;
  z-index: 2;
  &__berries{
    position: absolute;
    right: 12%;
    top: 200px;
    width: 189px;
    height: 164px;
    display: block;

  }
  &__berries2{
    width: 100px;
    height: 100px;
    display: block;
    transform: rotate(-140deg);
    position: absolute;
    right: 9%;
    top: -65px;
  }
  &__wrap {
    padding-top: 2.5%;
    padding-bottom: 80px;
  }
  &__block {
    position: relative;
    max-width: 700px;
    margin: 0 auto;

    >img {
      display: block;
      margin: -100px auto 0;
      max-width: 700px;
    }
  }
  &__title {
    font-size: px-to-rem(30px);
    line-height: px-to-rem(40px);
    letter-spacing: px-to-rem(1.5px);
    font-weight: bold;
    color: #fc2f5a;
    font-family: 'prosto_sans';
    text-transform: none;
    font-weight: 400;
    max-width: 660px;
  }
  &__subtitle {
    font-size: px-to-rem(36px);
    line-height: px-to-rem(48px);
    letter-spacing: px-to-rem(1.8px);
    text-transform: none;
    font-family: 'akrobat';
    color: #03be03;
    margin-top: 15px;
  }
  &__item {
    position: absolute;
    text-align: center;
    width: 280px;
    img {
      display: block;
      margin: 0 auto;
      width: 60px;
    }
    p {
      font-size: px-to-rem(14px);
      line-height: px-to-rem(22px);
      letter-spacing: px-to-rem(0.9px);
      font-family: 'prosto_sans';
      margin: 10px 0;
    }
    &:nth-of-type(1) {
      left: -10%;
      top: 12%;
    }
    &:nth-of-type(2) {
      left: -22%;
      top: 32%;
    }
    &:nth-of-type(3) {
      left: -20%;
      top: 52%;
    }
    &:nth-of-type(4) {
      left: -12%;
      top: 66%;
    }
    &:nth-of-type(5) {
      left: 0%;
      top: 82%;
      width: 350px;
    }
    &:nth-of-type(6) {
      left: 48%;
      top: 81%;
    }
    &:nth-of-type(7) {
      left: 74%;
      top: 75%;
    }
    &:nth-of-type(8) {
      left: 85%;
      top: 55%;
      width: 200px;
    }
    &:nth-of-type(9) {
      left: 80%;
      top: 32%;
      width: 258px;
    }
    &:nth-of-type(10) {
      left: 75%;
      top: 10%;
    }
  }
}
@media (max-width: 1550px) {
  .diagramm {
    background: none;

    &__berries {
      display: none;
    }

    //  &__title,&__subtitle{}
    //
    //  margin-left: 5%;
    //}
  }
  @media (max-width: 980px) {
    .diagramm {
      &__wrap {
        padding-bottom: 50px;
      }

      &__block {
        display: flex;
        flex-wrap: wrap;
        max-width: 100%;

        > img {
          display: none;
        }
      }

      &__item {
        position: static;
        flex-basis: 33%;
        max-width: 100%;
        margin: 20px 0;
      }
    }
  }

  @media (max-width: 768px) {
    .diagramm {
      &__title {
        font-size: px-to-rem(25px);
      }

      &__subtitle {
        font-size: px-to-rem(30px);
      }

      &__block {
        justify-content: space-between;
      }

      &__item {
        flex-basis: 48%;
      }
    }
  }

  @media (max-width: 480px) {
    .diagramm {
      &__block {
        flex-direction: column;
      }

      &__item {
        margin: 0 auto;
        flex-basis: auto;
      }
    }
  }
}