.first-screen {
  background: url("../img/first-screen-bg.jpg") center top no-repeat;
  &__slogan {
    font-size: px-to-rem(33px);
    line-height: 1.6;
    color: $green;
    margin-top: 8vh;
    margin-bottom: 18px;
    span {
      font-family: 'prosto_sans';
      text-transform: uppercase;
    }
  }
  &__wrap {
    padding-top: 10px;
    padding-bottom: 280px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      width: 148px;
      height: 141px;
      bottom: -145px;
      left: 90px;
      background: url("../img/list.png");
      z-index: 1;
    }
  }
  &__online {
    font-family: 'akrobat';
    font-size: px-to-rem(30px);
    line-height: 1.6;
    color: $menu-text;
    letter-spacing: px-to-rem(3px);
    margin: 0;
  }
  &__title {
    font-size: px-to-rem(30px);
    line-height: 1.389;
    text-transform: uppercase;
    color: $menu-text;
    max-width: 800px;
    margin: 0;
    letter-spacing: px-to-rem(1.8px);
    span {
      font-size: px-to-rem(33px);
      line-height: 1.2;
      font-family: 'prosto_sans';
      color: #37292c;
      letter-spacing: 1.65px;
      font-weight: 400;
      font-weight: 400;
    }
  }
  &__button {
    border-radius: 30px;
    background-color: $menu-text-active;
    display: block;
    width: 308px;
    text-decoration: none;
    font-size: px-to-rem(18px);
    line-height: px-to-rem(24px);
    letter-spacing: 0.9px;
    padding-top: 22px;
    padding-bottom: 22px;
    font-family: 'prosto_sans';
    text-align: center;
    margin: 25px 0;
    color: $white;
    letter-spacing: 0.9px;
    font-weight: 400;
    box-shadow: 2px 6px 0 0 #f07d9d;
    transition-duration: 500ms;
    transition-property: background-color, box-shadow;
    &:hover {
      background-color: darken($menu-text-active, 10%);
      box-shadow: 2px 6px 0 0 darken(#f07d9d, 5%);
    }
  }
  &__list {
    font-size: px-to-rem(26px);
    line-height: 1.733;
    text-transform: uppercase;
    color: $menu-text;
    margin-bottom: 100px;
    margin-top: 70px;

    p {
       padding-left: 65px;
       background: url("../img/apple_del.png") 10px center no-repeat;
       margin: 15px 0;
      height: 50px;
     }
    p:nth-child(2) {
      background: url("../img/ice-cream_del.png") 10px center no-repeat;
    }
    p:last-child {
       background: url("../img/bicycle_del.png") 0px center no-repeat;
     }
  }
}

@media (max-width: 1180px) {
  .first-screen {
    &__wrap {
      flex-wrap: wrap;
      padding-bottom: 100px;
    }

  }
}

@media (max-width: 768px) {
  .first-screen {
    &__title {
      font-size: px-to-rem(30px);
      span {
        font-size: px-to-rem(30px);
      }
    }
    &__slogan {
      margin-top: 50px;
    }
    &__list {
      margin-top: 50px;
      margin-bottom: 50px;
      font-size: px-to-rem(22px);
    }
  }
}
@media (max-width: 333px) {
  .first-screen__button{
    width: 280px;
  }
  .wrap{
    //overflow-x: hidden;
  }
  .first-screen__title, .first-screen__title span {
    font-size: 1.275rem;
    text-align: center;
  }
  .first-screen__list p{
    font-size: 20px;
  }
}
::active,::focus{
  border: none;
  outline: none;
}
