.steps {
  background-image: linear-gradient(to right, #fcdcd6 0%, #ffae9f 100%);
  position: relative;
  z-index: 1;

  &::after {
    content: "";
    height: 120%;
    width: 100%;
    background: url(../img/oblaka3.png) -1484px -155px no-repeat;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
  }
  &__subtitle {
    font-size: px-to-rem(30px);
    line-height: px-to-rem(40px);
    letter-spacing: px-to-rem(1.5px);
    font-family: 'prosto_sans';
    margin: 126px 0 0 0;
  }
  &__title {
    font-size: px-to-rem(30px);
    line-height: px-to-rem(52px);
    letter-spacing: px-to-rem(1.8px);
    color: #fc2f5a;
    font-family: 'akrobat';
    text-transform: none;
    margin: 0;
  }
  &__wrap {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  &__list {
    max-width: 1170px;
    margin: 40px auto;
    padding-left: 0;
  }
  &__item {
    display: flex;
    align-items: center;
    position: relative;
    span {
      margin-right: 10px;
      background: url("../img/scale.png") no-repeat;

      width: 150px;
      height: 100px;
      display: block;
    }

    p {
      font-size: px-to-rem(16px);
      line-height: px-to-rem(24px);
      letter-spacing: px-to-rem(0.9px);
      font-family: 'prosto_sans';
      max-width: 380px;
      margin: 0;
    }
    &:nth-child(1) {
      background: url("../img/arrow1.png") center top no-repeat;
      background-size: 200px;
    }
    &:nth-child(2) {
      &::after {
        background: url("../img/arrow2.png");
        -webkit-background-size: cover;
        background-size: cover;
        width: 100px;
        height: 101px;
        position: absolute;
        right: 260px;
        bottom: -100px;
        content: "";
      }
      margin-top: -30px;
      span {
        background: url("../img/diet_del.png") no-repeat;
        margin-left: auto;
      }
    }
    &:nth-child(3) {
      margin: 50px auto;
      max-width: 480px;
      &::after {
        background: url("../img/arrow3.png");
        width: 162px;
        height: 108px;
        position: absolute;
        left: -180px;
        top: 10px;
        content: "";
      }
      span{
        background: url("../img/salad_del.png") no-repeat;

      }
    }
    &:nth-child(4) {
      background: url("../img/arrow4.png") 500px top no-repeat;
      background-size: 200px;
      padding-bottom: 20px;
      span{
        background: url("../img/heart_del.png") no-repeat;

      }
    }
    &:nth-child(5) {
      &::after {
        background: url("../img/arrow5.png");
        background-size: cover;
        width: 230px;
        height: 163px;
        position: absolute;
        right: 420px;
        bottom: -160px;
        content: "";
      }
      span {
        margin-left: auto;
        background: url("../img/call-center_del.png") no-repeat;

      }
    }
    &:nth-child(6) {
      margin: 100px 0 50px;
    }
    span{
      background: url("../img/call_del.png") no-repeat;
    }
  }
  &__unique {
    font-size: px-to-rem(14px);
    line-height: px-to-rem(26px);
    letter-spacing: px-to-rem(0.9px);
    text-transform: uppercase;
    font-family: 'prosto_sans';
    color: $white;
    background-color: #fd8166;
    padding: 25px 20px 25px 40px;
    box-sizing: border-box;
    border-radius: 20px;
    max-width: 810px;
    position: relative;
    margin-bottom: 70px;
    &::after {
      content: "";
      background: url("../img/znak.png");
      width: 88px;
      height: 306px;
      position: absolute;
      right: -200px;
      top: -100px;
    }
  }
}

@media (max-width: 1100px) {
  .steps::after {
    background: url(../img/oblaka3.png) -1484px -455px no-repeat;
  }
  .steps {
    &__item {
      background: none !important;
      margin: 20px 0 !important;
      &::after {
        display: none;
      }
      &:nth-child(4), &:nth-child(6) {
        span {
          margin-left: auto;
        }
      }
      &:nth-child(5) {
        span {
          margin-left: 0;
        }
      }
    }
    &__unique {
      max-width: 100%;
      margin-bottom: 40px;
      &::after {
        display: none;
      }
    }
  }
}

@media (max-width: 580px) {
  .steps {
    &__unique {
      font-size: px-to-rem(16px);
    }
    &__item {
      span {
        font-size: px-to-rem(70px);
        width: 90px;
        background-size: 100% !important;
      }
    }
  }
}