.programm {
  position: relative;
  z-index: 0;
  max-width: 1920px;
  margin: 0 auto;
  display: flex;
  background: url(../img/herb.png) 26% 90% no-repeat;


  &::before {
    //background: url("../img/uzor.png") repeat #f5b4c1;
    width: 50%;
    padding-bottom: 45%;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
  }
  &__left{
    display: block;
    width: 455px;
    margin: 1px 0px 0px 242px;
  }
  &__left img{
    width: 100%;
    height: 100%;
  }
  &__paragraph{
    margin-top: 32px;
    letter-spacing: 0.9px;
    line-height: 28px;
    font-weight: 400;
    font-size: 18px;
    color: #37292c;

  }
  &__paragraph span{
    height: 30px;
    display: block;
  }
  &__wrap {
    margin-left: 80px;
  }
  &__info {
    width: 705px;
    margin-top: 190px;
  }
  &__slogan {
    width: 537px;
    margin-top: 0;
    padding-top: 40px;
    font-size: px-to-rem(30px);
    line-height: px-to-rem(40px);
    letter-spacing: px-to-rem(1.5px);
    font-family: 'prosto_sans';
    text-transform: none;
    margin-bottom: 20px;
  }
  &__title {
    font-size: px-to-rem(30px);
    line-height: px-to-rem(42px);
    letter-spacing: px-to-rem(1.5px);
    font-family: 'akrobat';
    color: #fc2f5a;
    font-weight: 700;
    margin-top: 20px;
    margin-bottom: 30px;
  }
  &__steps {
    font-size: px-to-rem(24px);
    line-height: px-to-rem(24px);
    letter-spacing: px-to-rem(0.9px);
    margin-top: 55px;
    margin-bottom: 4%;
    font-family: 'prosto_sans';
    margin-left: 15px;
  }
  &__step {
    display: flex;
    align-items: center;
    margin: 14px 0;

    > span {
      font-size: px-to-rem(65px);
      line-height: px-to-rem(75px);
      letter-spacing: px-to-rem(4px);
      font-weight: 700;
      color: #fde7e2;
      margin-right: 15px;
    }
    p {
      font-size: px-to-rem(16px);
      line-height: px-to-rem(24px);
      letter-spacing: px-to-rem(0.9px);
      background-color: #fde7e2;
      border-radius: 40px;
      padding: 48px 58px 48px 48px;
      font-family: 'prosto_sans';
      margin: 0 25px 0 0;
      width: 73%;
      max-width: 925px;
    }
  }
  &__garanty {
    display: flex;
    align-items: center;
    margin: 6% 0;

    img {
      margin-right: 30px;
      width: 130px;
    }
    p {
      font-size: px-to-rem(26px);
      line-height: px-to-rem(34px);
      letter-spacing: px-to-rem(1.8px);
      font-family: 'akrobat';
      color: #fc2f5a;
      text-transform: uppercase;
      margin: 0;
      flex-basis: calc(100% - 180px);
    }
  }
}

@media (max-width: 1510px) {
  .programm {
    &__left {
      position: absolute;
      left: 0;
      bottom: -5px;
      margin: 0;
    }
    &__wrap{
      position: relative;
      margin: 0;
      width: 100% !important;
      max-width: 100%;

    }
    &__info{
      margin-left: auto;
      width: 60%;
    }
  }



}














@media (max-width: 1280px) {
  .programm {
    &::after {
      top: 50%;
      transform: translateY(-50%);
    }
    &__left{
      width: 35vw;
      height: 80vw;
    }
  }
}

@media (max-width: 1030px) {
  .programm {
    &::after {
      display: none;
    }
    &__left{
      display: none;
    }
    &__info {
      width: 100%;
      padding-bottom: 30px;
    }
    &__garanty {
      margin: 30px 0;
    }
    &__step {
      margin: 25px 0;
      p {
        margin-right: 0;
      }
    }
  }
}

@media (max-width: 768px) {
  .programm {
    &__slogan{
      width: 100%;
    }
    &__title, &__slogan {
      font-size: px-to-rem(28px);
    }
    &__garanty {
      p {
        font-size: px-to-rem(28px);
      }
    }
  }
}

@media (max-width: 580px) {
  .programm {
    &__step {
      flex-direction: column;
      >span {
        margin-right: 0;
        margin-bottom: 15px;
      }
      p {
        padding: 20px;
        border-radius: 30px;
      }
    }
    &__garanty {
      flex-direction: column;
      img {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }
  }
}