@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap&subset=cyrillic');

@font-face {
  font-family: 'prosto_sans';
  src: url('../fonts/tt_prosto_sans_light_italic-webfont.woff2') format('woff2'),
  url('../fonts/tt_prosto_sans_light_italic-webfont.woff') format('woff');
  font-weight: 300;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: 'prosto_sans';
  src: url('../fonts/tt_prosto_sans_regular-webfont.woff2') format('woff2'),
